@import url(https://fonts.googleapis.com/css2?family=Dhurjati&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dhurjati&display=swap);

body {
  margin: 0;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
background-image: linear-gradient(to top, #fff1eb 0%, #ace0f9 100%);
}
.navbar{
    background-color: black;
    display:flex;
    justify-content: center;
}

.navbar a{
 color:white;
 padding: 24px 26px;
 text-decoration: none;
 font-size:30px;
 font-family: 'Dhurjati', sans-serif;
 

}

.navbar a:hover{
    background-color:#ace0f9;
    color:black;
}
.header {
    font-family: 'Dhurjati', sans-serif;
    font-size:60px;
    text-align:center;
}
.card-container {
    display:flex;
    flex-wrap:wrap;
    justify-content:space-evenly;
}
.cards{
    padding : 20px;
    background-color:black;
    border-radius:7px;
    box-shadow: 3px 3px 5px rgba(0,0,0,0.7);
    margin: 1rem;
    position: relative;
    overflow:hidden;
}

.title{
    color:white;
    font-family: 'Dhurjati', sans-serif;
    font-size: 36px;
    padding:1rem;
}

.cards img{
   
    object-fit: cover;
    height: 350px;
    width: 500px;
    -webkit-filter:grayscale(0%);
            filter:grayscale(0%);
   

}

.cards img:hover{
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
}

.card-over{
    position: absolute;
    background-color:rgba(0,0,0, 0.5);
    color:white;
    bottom: 0;
    left : 0;
    right: 0;
    padding:1rem;
    -webkit-transform: translate(100%);
            transform: translate(100%);
    transition:-webkit-transform 0.7s ease-n-out;
    transition:transform 0.7s ease-n-out;
    transition:transform 0.7s ease-n-out, -webkit-transform 0.7s ease-n-out;

}
 
.cards:hover .card-over{
    -webkit-transform: translateY(0%);
            transform: translateY(0%)

}
