.card-container {
    display:flex;
    flex-wrap:wrap;
    justify-content:space-evenly;
}
.cards{
    padding : 20px;
    background-color:black;
    border-radius:7px;
    box-shadow: 3px 3px 5px rgba(0,0,0,0.7);
    margin: 1rem;
    position: relative;
    overflow:hidden;
}

.title{
    color:white;
    font-family: 'Dhurjati', sans-serif;
    font-size: 36px;
    padding:1rem;
}

.cards img{
   
    object-fit: cover;
    height: 350px;
    width: 500px;
    filter:grayscale(0%);
   

}

.cards img:hover{
    filter: grayscale(100%);
}

.card-over{
    position: absolute;
    background-color:rgba(0,0,0, 0.5);
    color:white;
    bottom: 0;
    left : 0;
    right: 0;
    padding:1rem;
    transform: translate(100%);
    transition:transform 0.7s ease-n-out;

}
 
.cards:hover .card-over{
    transform: translateY(0%)

}